import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'

import { apipost } from './../../classes/request'
const { setComp } = global.rootRequire('redux')
class registerComponent extends Component {
  constructor (props) {
    super(props)
    this.state = {
      editingData: {},
      msg: ''
    }
  }

  onBlurData (e, name) {
    let editingData = this.state.editingData
    editingData[name] = e.target.value
    this.setState({ editingData: editingData })
  }

  onClickRegisterSubmit (e) {
    let editingData = this.state.editingData

    this.setState({ editingData: editingData }, () => {
      apipost(global.rxu.config.api_user, this.state.editingData, {
        '1': (json) => {
          this.setState({ msg: '' })
          // this.props.history.push('login')
          this.props.setComp('login')
        },
        '-2': (json) => {
          var strmsg = ''
          if (json.msg === 'Dupplicate data') {
            strmsg = 'Email  c ng k'
          } else if (json.msg === 'Email invalid format') {
            strmsg = 'nh dng Email khng ng'
          } else {
            strmsg = 'Cc trng * khng c  trng'
          }
          this.setState({ msg: strmsg })
        }
      })
    })
  }

  render () {
    return (
      <div className='authloginpage'>
        <div className='authform'>
          <div className='register-form'>
            <div className='authform__head'>ng k</div>
            <p className='authform_msgerror'>{this.state.msg}</p>
            <input tabIndex='1' type='text' placeholder='Ti khon' className='authform__input' onChange={(e) => this.onBlurData(e, 'username')} />
            <input tabIndex='2' type='text' placeholder='Tn y ' className='authform__input' onChange={(e) => this.onBlurData(e, 'fullname')} />
            <input tabIndex='3' type='text' placeholder='Email' className='authform__input' onChange={(e) => this.onBlurData(e, 'email')} />
            <input tabIndex='4' type='text' placeholder='S in thoi' className='authform__input' onChange={(e) => this.onBlurData(e, 'phone')} />
            <input tabIndex='5' type='text' placeholder='a ch' className='authform__input' onChange={(e) => this.onBlurData(e, 'address')} />
            <input tabIndex='6' type='password' placeholder='Mt khu' className='authform__input' autoComplete='new-password' onChange={(e) => this.onBlurData(e, 'password')} />
            <input tabIndex='7' type='password' placeholder='Xc nhn mt khu' className='authform__input' onChange={(e) => this.onBlurData(e, 'repassword')} />
            <div tabIndex='10' className='authform__btnsubmit' onClick={(e) => this.onClickRegisterSubmit(e)} onKeyPress={(e) => this.onClickRegisterSubmit(e)} >To ti khon</div>
            <p className='authform__msg'>Bn  c ti khon? <Link to='/login'>ng nhp</Link></p>
          </div>
        </div>
      </div>
    )
  }
}
const mapStateToProps = (state) => ({
})
const mapDispatchToProps = { setComp }

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(registerComponent)

